@import"https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap";.body{background:#e6e3d9 !important;height:100vh;position:relative}.background{width:100%;height:100vh;background:url("../../assets/images/bg.webp") no-repeat;background-size:cover;background-position:center;filter:blur(10px)}.container{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:75%;height:500px;background:url("../../assets/images/bg-3.png") no-repeat;background-size:cover;background-position:right;border-radius:10px;margin-top:20px}.container .content{position:absolute;top:0;left:0;width:58%;height:100%;background:rgba(0,0,0,0);padding-top:80px;padding-bottom:80px;padding-right:5px;padding-left:130px;padding-right:80px;color:#162938;display:flex;justify-content:space-between;flex-direction:column}.content .logo{font-size:30px}.text-sci h2{font-size:40px;line-height:1}.text-sci h2 span{font-size:25px}.text-sci p{font-size:16px;margin:20px 0}.social-icons i{font-size:30px;color:#162938;margin-right:30px;transition:.5s ease}.social-icons i:hover{transform:scale(1.2)}.container .logreg-box{position:absolute;top:0;right:0;width:42%;height:100%;overflow-y:auto;overflow-x:hidden}.container .logreg-box::-webkit-scrollbar{width:4px}.container .logreg-box::-webkit-scrollbar-track{background:rgba(0,0,0,0)}.container .logreg-box::-webkit-scrollbar-thumb{background:#91704f;border-radius:3px}.container .logreg-box::-webkit-scrollbar-thumb:hover{background:#755a3f}.logreg-box .form-box{position:absolute;display:flex;justify-content:center;align-items:center;width:100%;height:100%;background:rgba(0,0,0,0);backdrop-filter:blur(20px);border-top-right-radius:10px;border-bottom-right-radius:10px;color:#162938}.logreg-box .form-box.login{transform:translateX(0px);transition:transform .6s ease;transition-delay:.7s}.logreg-box.active .form-box.login{transform:translateX(430px);transition-delay:0s}.logreg-box .form-box.register{transform:translateX(430px);transition:transform .6s ease;transition-delay:0s}.logreg-box.active .form-box.register{transform:translateX(0px);transition-delay:.7s}.form-box h2{font-size:32px;text-align:center}.form-box h2{font-size:32px;text-align:center}.form-box .input-box{width:340px;height:50px;position:relative;border-bottom:2px solid #162938;margin:15px 0}.input-box input{width:100%;height:100%;background:rgba(0,0,0,0);border:none;outline:none;font-size:16px;font-weight:500;padding-right:28px}.input-box label{position:absolute;top:50%;left:0;transform:translateY(-50%);font-size:16px;font-weight:500;pointer-events:none;transition:.5s ease}.input-box input:focus~label,.input-box input:valid~label{top:-5px}.input-box .icon{position:absolute;top:13px;right:0;font-size:19px}.form-box .remember-forgot{font-size:14.5px;font-weight:500;margin:15px 0 15px;display:flex;justify-content:space-between}.remember-forgot label input{accent-color:#162938;margin-right:3px}.remember-forgot a{color:#162938;text-decoration:none}.remember-forgot a:hover{text-decoration:underline}.btn{width:100%;height:45px;background:#91704f;border:none;outline:none;border-radius:4px;cursor:pointer;font-size:16px;color:#e4e4e4;font-weight:500;box-shadow:0 0 10px ☐ rgba(0,0,0,.5)}.form-box .login-register{font-size:14.5px;font-weight:500;text-align:center;margin-top:25px}.login-register p a{color:#162938;font-weight:600;text-decoration:none}.login-register p a:hover{text-decoration:underline}.error-messsage{margin-top:-10px;font-size:10px}.exclamation{color:#eb001b}.title{padding-top:40px}